import React from "react";
import Grid from '@material-ui/core/Grid';

import Item from "../shop/Item";
import "../shop/shop.css";

//  import itemsList from "./shop.data";
import Loading from "../skeleton/Skeleton"
function SearchFoundItems(props){
    const NotFound=()=>{
        return <div className="not-found">
        <h1>OOPS!</h1>
        <h2>No Results Found! Try Again.</h2>
        </div>
    }
const {items,isLoading}=props;
console.log(items);
    return <div >
    <Grid container spacing={1}>
    {isLoading&&<div><Loading /></div>}
     {  items.length!==0?items.map((item)=>{
      return (<Item item={item} key={item._id} imageUrl={item.imageUrl} name={item.name} price={item.price}/>)
     }):<NotFound />}
    </Grid>
    </div>
    
    ;}
export default SearchFoundItems;
