import React from "react";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
const ContactForm = () => {
  return (
    <form className="contact-from">
      <div className="contact-name">
        <TextField
          className="contact-input"
          margin="normal"
          label="First Name"
          name="fName"
          autoComplete={false}
          autoFocus
          required
        />
        <TextField
          margin="normal"
          label="Last Name"
          name="lName"
          autoComplete={false}
          autoFocus
          className="contact-input"
          required
        />
      </div>
      <div className="contact-email">
        <TextField
          className="contact-input"
          margin="normal"
          required
          id="email"
          label="Email"
          name="email"
          autoComplete="email"
          autoFocus
        />
        <TextField
          required
          className="contact-input"
          margin="normal"
          label="Subject"
          name="subject"
        />
      </div>

      <TextField
          required
          multiline={true}
          rows="7"
          className="contact-input message"
          variant="outlined"
          margin="normal"
          label="Message"
          name="message"
          autoComplete={false}
          autoFocus
          placeholder="Write your message.."
        />

      <Button type="submit" color="inherit" className="contact-button">
        Send Message
      </Button>
    </form>
  );
};
export default ContactForm;
