import React,{useState} from 'react';
import Input from '@material-ui/core/Input';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import SearchIcon from '@material-ui/icons/Search';
import Button from "@material-ui/core/Button";
import SearchFoundItems from"./SearchFoundItems"
import "./search-styles.css";
import axios from 'axios';
const SearchForm =()=>{
const [isLoading,SetIsLoading]=useState(null);
const [query,setQuery]=useState("");
const [items,setItems]=useState([]);
    const fetchSearchedData=async (q)=>{
        SetIsLoading(true);
   const response = await axios.get(`/api/find/item?input=${q}`);
   setItems(response.data);
   SetIsLoading(false);
   
    }
    const handleSubmit=(event)=>{
        setItems([]);
        if(query!=="")
        {
            fetchSearchedData(query);
        }
        event.preventDefault()
    }
    const handleChange=(event)=>{
    const {value}=event.target;
    setQuery(value)
    }
    return <div>
    <form className="search-from" onSubmit={handleSubmit}>
    <FormControl  className="search-label">
    <Input
    onChange={handleChange}
    value={query}
    fullWidth
    rows={2}
      placeholder="Search"
      disableUnderline={true}
      startAdornment={
        <InputAdornment position="start">
          <SearchIcon className="search-icon" />
        </InputAdornment>
      }
      endAdornment={
        <InputAdornment position="end">
        <Button type="submit" className="lookup-button">LOOK UP</Button>
      </InputAdornment>
      }
    />
  </FormControl>
    </form>
    {isLoading!==null&&<SearchFoundItems items={items} isLoading={isLoading} />}
     </div>
}
export default SearchForm;
