import React from "react";
import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";
import RoomIcon from "@material-ui/icons/Room";
import EmailIcon from "@material-ui/icons/Email";
import PhoneIcon from "@material-ui/icons/Phone";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import TwitterIcon from "@material-ui/icons/Twitter";
import IconButton from "@material-ui/core/IconButton";
const ContactInformation = () => {
  return (
    <div className="contact-information">
      <h2 style={{marginLeft: "25px",
        lineHeight: "1.5",fontWeight: "bold"}}>Contact information</h2>
      <p style={{marginLeft: "25px",
      lineHeight: "1.5"}}>Fill up the form and our Team will get back to you within 24 hours.</p>
      <ul>
        <li>
          <PhoneIcon />
          <span>+2126 2408 0275</span>
        </li>
        <li>
          <EmailIcon />
          <span>crownclothingsupport@gmail.com</span>
        </li>
        <li>
          <RoomIcon />
          <span>102 Street 2714 Don</span>
        </li>
      </ul>
      <div className="social-media">
        <IconButton>
          <FacebookIcon className="social-media-icon" />
        </IconButton>
        <IconButton>
          <InstagramIcon className="social-media-icon" />
        </IconButton>
        <IconButton>
          <LinkedInIcon className="social-media-icon" />
        </IconButton>
        <IconButton>
          <TwitterIcon className="social-media-icon" />
        </IconButton>
      </div>
    </div>
  );
};
export default ContactInformation;
